import React, { useEffect, useState } from 'react';
import { Panel, PanelHeader, Tabbar, TabbarItem, View, SplitLayout, SplitCol, Group, FixedLayout, Div, Snackbar, Card, CardGrid } from '@vkontakte/vkui';
import { Icon28MarketOutline, Icon28MoneyCircleOutline, Icon16Done, Icon16Cancel } from '@vkontakte/icons';
import Backend from './backend.js';
import { BrowserRouter as Router } from "react-router-dom";
import './App.css';

function App() {
  const [activePanel, setActivePanel] = useState('clicker');
  const [balance, setBalance] = useState(0);
  const [clickPower, setClickPower] = useState(1);
  const [passiveIncome, setPassiveIncome] = useState(0);
  const [snackbar, setSnackbar] = useState(null); //   
  const [upgradeCosts, setUpgradeCosts] = useState({
    clickPowerOne: 100,
    passiveIncomeOne: 100,
    clickPowerTwo: 200,
    passiveIncomeTwo: 200,
    clickPowerThr: 500,
    passiveIncomeThr: 500,
    clickPowerFive: 1000,
    passiveIncomeFive: 1000,
  });
  
  useEffect(() => {
    const initializeBackend = async () => {
      await Backend.init(); 
      const userData = await Backend.loadUserData();
      setBalance(userData.balance);
      setClickPower(userData.clickPower);
      setPassiveIncome(userData.passiveIncome);
      setUpgradeCosts(userData.upgradeCosts);
    };
  
    initializeBackend();
  }, []);
  

  useEffect(() => {
    const interval = setInterval(() => {
      setBalance(prevBalance => {
        const newBalance = prevBalance + passiveIncome;
        Backend.balance = newBalance;
        Backend.saveUserData();
        return newBalance;
      });
    }, 1000);

    return () => clearInterval(interval);
  }, [passiveIncome]);

  const handleCatClick = () => {
    const result = Backend.handleTap();
    if (result.success) {
      setBalance(result.balance);
    } else {
      showSnackbar(result.message, false);
    }
  };

  const handleShowAd = async () => {
    const result = await Backend.handleShowAdBack();
    if (result.success) {
      setBalance(result.balance);
      showSnackbar('  .  .', true);
    } else {
        showSnackbar('  .', false);
    }
  };

  //  ,    2 
  const showSnackbar = (message, isSuccess) => {
    setSnackbar(
      <Snackbar
        onClose={() => setSnackbar(null)}
        before={isSuccess ? <Icon16Done fill="var(--vkui--color_icon_positive)" /> : <Icon16Cancel fill="var(--vkui--color_icon_negative)" />}
        duration={500}
      >
        {message}
      </Snackbar>
    );
  };

  const handleUpgrade = (upgradeType) => {
    const result = Backend.purchaseUpgrade(upgradeType);
    if (result.success) {
      setBalance(result.balance);
      setClickPower(result.clickPower);
      setPassiveIncome(result.passiveIncome);
      setUpgradeCosts(prevCosts => ({ ...prevCosts, [upgradeType]: result.newCost }));
      showSnackbar(' ', true);
    } else {
      showSnackbar(' ', false);
    }
  };
  

  return (
    <Router basename="/vk-clicker-app">
    <SplitLayout popout={snackbar}>
      <SplitCol>
        <View activePanel={activePanel}>
          {/*   */}
          <Panel id="clicker">
          <PanelHeader></PanelHeader>
            <Group>
              <Div className="clicker-content">
                <p>  !</p>
                <img  src={`${process.env.PUBLIC_URL}/cat.png`} alt="" className="clicker-cat" onClick={handleCatClick} style={{
                    width: 'clamp(170px, 40vw, 270px)',
                    height: 'auto',
                    cursor: 'pointer',
                    backgroundColor: 'transparent',
                  }}/>
                <p>: {balance} </p>
                <button className="button" onClick={handleShowAd}>  (x2 )</button>
              </Div>
            </Group>
          </Panel>

          {/*   */}
          <Panel id="shop">
          <PanelHeader></PanelHeader>
          <Group>
          <Div className="upgrades-grid">
                {[
                    { name: ' ', aboutUp: "+1  ", upgradeType: 'clickPowerOne', image: `${process.env.PUBLIC_URL}/devCat.jpg` },
                    { name: ' ', aboutUp: "+1 /", upgradeType: 'passiveIncomeOne', image: `${process.env.PUBLIC_URL}/businessCat.jpg` },
                    { name: ' ', aboutUp: "+2  ", upgradeType: 'clickPowerTwo', image: `${process.env.PUBLIC_URL}/energyCat.jpg` },
                    { name: ' ', aboutUp: "+2 /", upgradeType: 'passiveIncomeTwo', image: `${process.env.PUBLIC_URL}/smartCat.jpg` },
                    { name: ' ', aboutUp: "+3  ", upgradeType: 'clickPowerThr', image: `${process.env.PUBLIC_URL}/coolCat.jpg` },
                    { name: ' ', aboutUp: "+3 /", upgradeType: 'passiveIncomeThr', image: `${process.env.PUBLIC_URL}/fatCat.jpg` },
                    { name: ' ', aboutUp: "+5  ", upgradeType: 'clickPowerFive', image: `${process.env.PUBLIC_URL}/wiseCat.jpg`},
                    { name: ' ', aboutUp: "+5 /", upgradeType: 'passiveIncomeFive', image: `${process.env.PUBLIC_URL}/richCat.jpg`}
                ].map((item, index) => (
                    <Card key={index} mode='shadow' className="upgrade-card" style={{
                    backgroundImage: `url(${item.image})`,
                    backgroundSize: 'cover',
                    backgroundPosition: 'center'}}>
                    <div className="upgrade-text">
                        <p>{item.name}</p>
                        <p>{item.aboutUp}</p>
                        <p>: {upgradeCosts[item.upgradeType]} </p>
                    </div>
                    <button className="button" onClick={() => handleUpgrade(item.upgradeType)}>
                        
                    </button>
                    </Card>
                ))}
            </Div>
            </Group>
            
          </Panel>
        </View>

        {/*    */}
        <FixedLayout vertical="bottom">
          <Tabbar>
            <TabbarItem
              selected={activePanel === 'clicker'}
              onClick={() => setActivePanel('clicker')}
              text=""
            >
              <Icon28MoneyCircleOutline />
            </TabbarItem>
            <TabbarItem
              selected={activePanel === 'shop'}
              onClick={() => setActivePanel('shop')}
              text=""
            >
              <Icon28MarketOutline />
            </TabbarItem>
          </Tabbar>
        </FixedLayout>
      </SplitCol>
    </SplitLayout>
    </Router>
  );
}

export default App;
