export const noop = () => {
  //    
};

/**
 *  ,   `fn`   .  
 *  .
 *
 * @example
 * ```ts
 * import {once} from '@vkontakte/vkjs';
 *
 * let counter = 0;
 * const onceFn = once(() => counter++);
 *
 * onceFn();
 * onceFn();
 * onceFn();
 *
 * console.log(counter); // 1
 * ```
 *
 * @param fn ,      
 */
export function once<T extends (...args: any) => any>(fn: T) {
  // TODO: once   ,     
  let called = false;
  return function (...args) {
    if (called) {
      return;
    }

    called = true;
    return fn.apply(this, args);
  } as T;
}
